export default{
	data() {
		return {
			userInfo: {},
			loginHide: true
		}
	},
	onLoad() {
		var that = this;
		uni.checkSession({
			success(infoRes) {
				console.log(infoRes)
				if(infoRes){
					that.loginHide = false;
				}else{
					
				}
			}
		});
		this.getUserInfo()
	},
	methods:{
		getUserInfo(){
			var that = this;
			uni.getUserInfo({
				provider: 'weixin',
				success: function (infoRes) {
					uni.showToast({
						title: '登录成功',
						duration: 2000
					});
					that.loginHide = false;
					that.userInfo = infoRes.userInfo;
					//console.log(infoRes)
				},
				fail: function () {
					console.log('登陆失败')
				}
			})
		}
	}
}
